* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list{
  list-style-type: none;
  
}
.country-item{
  display: flex;
  align-items: center;
  gap:30px;
  margin-top: 30px;
}
.img-wrapper{
  display: flex;
  align-items: center;
  gap: 20px;
}
.country-title{
  font-size:32px;
}
.description{
  font-size: large;
}
.subdescription{
  font-weight: 700;
}